<!-- =========================================================================================
    File Name: AgGridTable.vue
    Description: Ag Grid table
    ----------------------------------------------------------------------------------------
    Item Name: Vuexy - Vuejs, HTML & Laravel Admin Dashboard Template
    Author: Pixinvent
    Author URL: http://www.themeforest.net/user/pixinvent
========================================================================================== -->

<template>
  <div id="ag-grid-demo">
    <vx-card>
      <!-- TABLE ACTION ROW -->
      <div class="flex flex-wrap justify-between items-center">
        <!-- ITEMS PER PAGE -->
        <div class="mb-4 md:mb-0 mr-4 ag-grid-table-actions-left">
          <vs-dropdown vs-trigger-click class="cursor-pointer">
            <div
              class="p-4 border border-solid d-theme-border-grey-light rounded-full d-theme-dark-bg cursor-pointer flex items-center justify-between font-medium"
            >
              <span class="mr-2"
                >{{ currentPage * paginationPageSize - (paginationPageSize - 1) }} -
                {{
                  realStocks.length - currentPage * paginationPageSize > 0
                    ? currentPage * paginationPageSize
                    : realStocks.length
                }}
                of {{ realStocks.length }}</span
              >
              <feather-icon icon="ChevronDownIcon" svgClasses="h-4 w-4" />
            </div>
            <!-- <vs-button class="btn-drop" type="line" color="primary" icon-pack="feather" icon="icon-chevron-down"></vs-button> -->
            <vs-dropdown-menu>
              <vs-dropdown-item @click="gridApi.paginationSetPageSize(20)">
                <span>20</span>
              </vs-dropdown-item>
              <vs-dropdown-item @click="gridApi.paginationSetPageSize(50)">
                <span>50</span>
              </vs-dropdown-item>
              <vs-dropdown-item @click="gridApi.paginationSetPageSize(100)">
                <span>100</span>
              </vs-dropdown-item>
              <vs-dropdown-item @click="gridApi.paginationSetPageSize(150)">
                <span>150</span>
              </vs-dropdown-item>
            </vs-dropdown-menu>
          </vs-dropdown>
        </div>

        <!-- TABLE ACTION COL-2: SEARCH & EXPORT AS CSV -->
        <div
          class="flex flex-wrap items-center justify-between ag-grid-table-actions-right"
        >
          <vs-input
            class="mb-4 md:mb-0 mr-4"
            v-model="searchQuery"
            @input="updateSearchQuery"
            placeholder="Chercher..."
          />
          <template>
            <div>
              <vs-dropdown vs-custom-content vs-trigger-click class="cursor-pointer">
                <feather-icon
                  icon="ShoppingCartIcon"
                  class="cursor-pointer ml-4 mr-6 mt-1"
                  :badge="cartItems.length"
                />
                <vs-dropdown-menu
                  class="cart-dropdown vx-navbar-dropdown"
                  :class="{ 'dropdown-custom': cartItems.length }"
                >
                  <!-- IF CART HAVE ITEMS: HEADER -->
                  <template v-if="cartItems.length">
                    <div
                      class="notification-header text-center p-5 bg-primary text-white"
                    >
                      <h3 class="text-white">
                        {{ cartItems.length }} Produit<span v-show="cartItems.length > 1"
                          >s</span
                        >
                      </h3>
                    </div>

                    <!-- CART ITEMS -->
                    <VuePerfectScrollbar
                      ref="mainSidebarPs"
                      class="scroll-area--cart-items-dropdowm p-0 mb-10"
                    >
                      <ul class="bordered-items">
                        <li
                          v-for="item in cartItems"
                          :key="item.id"
                          class="vx-row no-gutter cart-item cursor-pointer"
                        >
                          <!-- INFO COL -->
                          <div
                            class="vx-col w-4/5 pr-4 pl-2 py-4 flex flex-col justify-center ml-5"
                          >
                            <span
                              class="font-medium block cart-item-title truncate"

                              >{{ item.orderedItemData.item }}</span
                            >
                            <div class="flex items-center justify-between">
                              <span class="text-sm font-medium"
                                >{{ item.quantity }} <small>x</small>
                                {{ item.unitPrice }} TND</span
                              >
                              <feather-icon
                                icon="XIcon"
                                svgClasses="h-4 w-4 cursor-pointer text-danger"
                                class="hover:text-danger"
                                @click="DeleteItem(item)"
                              />
                            </div>
                          </div>
                        </li>
                      </ul>
                    </VuePerfectScrollbar>
                    <div
                      class="checkout-footer fixed bottom-0 rounded-b-lg text-primary font-semibold w-full p-2 text-center border border-b-0 border-l-0 border-r-0 border-solid d-theme-border-grey-light cursor-pointer"
                    >
                      <span class="flex items-center justify-center">
                        <feather-icon icon="ShoppingCartIcon" svgClasses="h-4 w-4" />
                        <span class="ml-2" @click="Add">Valider </span>
                      </span>
                    </div>
                  </template>

                  <!-- IF CART IS EMPTY -->
                  <template v-else>
                    <p class="p-4">Vous n'avez pas des produits</p>
                  </template>
                </vs-dropdown-menu>
              </vs-dropdown>
            </div>
          </template>

          <vs-button
            color="success"
            class="mb-4 md:mb-0 mr-5"
            @click="gridApi.exportDataAsCsv()"
            >Exporter CSV</vs-button
          >
          <vs-button icon-pack="feather" icon="icon-plus" @click="activePromptAddRealStock = true"
            >Ajouter</vs-button
          >
        </div>
      </div>
      <ag-grid-vue
        @selection-changed="onSelectionChanged"
        ref="agGridTable"
        :gridOptions="gridOptions"
        class="ag-theme-material w-100 my-4 ag-grid-table"
        :columnDefs="columnDefs"
        :defaultColDef="defaultColDef"
        :rowData="realStocks"
        rowSelection="multiple"
        colResizeDefault="shift"
        :animateRows="true"
        :floatingFilter="true"
        :pagination="true"
        :paginationPageSize="paginationPageSize"
        :suppressPaginationPanel="true"
        :enableRtl="$vs.rtl"
      >
      </ag-grid-vue>
      <vs-pagination :total="totalPages" :max="maxPageNumbers" v-model="currentPage" />
    </vx-card>
    <!-- ADD REAL STOCK -->
    <vs-prompt
      class="calendar-event-dialog"
      title="Ajouter produit"
      accept-text="Ajouter"
      cancel-text="Annuler"
      @accept="AddRealStock"
      :active.sync="activePromptAddRealStock"
    >
      <vs-input
        type="text"
        class="w-full mt-5 mb-4"
        label-placeholder="Produit"
        v-model="realStock.item"
      ></vs-input>
      <vs-input
        type="number"
        class="w-full mt-5 mb-4"
        label-placeholder="Quantité"
        v-model="realStock.quantity"
      ></vs-input>
      <vs-input
        type="number"
        class="w-full mt-5 mb-4"
        label-placeholder="Prix unitaire"
        v-model="realStock.unitPrice"
      ></vs-input>
      <vs-input
        type="text"
        class="w-full mt-5 mb-4"
        label-placeholder="Fournisseur (optionnel)"
        v-model="realStock.vendor"
      ></vs-input>
      <vs-input
        type="text"
        class="w-full mt-5 mb-4"
        label-placeholder="Tél fournisseur (optionnel)"
        v-model="realStock.vendorTel"
      ></vs-input>
    </vs-prompt>

    <!-- EDIT REAL STOCK -->
    <vs-prompt
      class="calendar-event-dialog"
      title="Détails produit"
      accept-text="Modifier"
      cancel-text="Supprimer"
      @accept="PutRealStock"
      @cancel="DeleteRealStock"
      :active.sync="activePromptEditRealStock"
    >
      <vs-input
        class="w-full mt-5 mb-4"
        label-placeholder="Produit"
        v-model="realStockData.item"
      ></vs-input>
      <vs-input
        type="number"
        class="w-full mt-5 mb-4"
        label-placeholder="Quantité"
        v-model="realStockData.quantity"
      ></vs-input>
      <vs-input
        type="number"
        class="w-full mt-5 mb-4"
        label-placeholder="Prix unitaire"
        v-model="realStockData.unitPrice"
      >
      </vs-input>
      <vs-input
        class="w-full mt-5 mb-4"
        label-placeholder="Fournisseur (optionnel)"
        v-model="realStockData.vendor"
      ></vs-input>
      <vs-input
        class="w-full mt-5 mb-4"
        label-placeholder="Tél fournisseur (optionnel)"
        v-model="realStockData.vendorTel"
      ></vs-input>
      <div>
        <vs-button style="font-size: 90%" type="line" @click.prevent="SetQuote" @click="activePromptAddOrderedItems = true"
          >Devis</vs-button
        >
        <vs-button style="font-size: 90%" type="line" color="success" @click.prevent="SetInvoice" @click="activePromptAddOrderedItems = true">Facture</vs-button>
        <vs-button style="font-size: 90%" type="line" color="danger" @click.prevent="SetRental" @click="activePromptAddOrderedItems = true">B.Loc</vs-button>
        <vs-button style="font-size: 90%" type="line" color="dark">B.Liv</vs-button>
      </div>
      <div style="text-align: center; margin-top: 10px; padding: 10px" class="mt-4">
        <vs-button style="font-size: 90%" type="border" class="mr-3" color="success"
          >Facture & B.Liv</vs-button
        >
        <vs-button style="font-size: 90%" type="border" color="danger"
          >B.Loc & B.Liv</vs-button
        >
      </div>
    </vs-prompt>

    <!-- ADD QUOTE -->

    <vs-popup title="Ajouter produit" :active.sync="activePromptAddOrderedItems">
      <vs-input
        class="w-full mt-5 mb-4"
        label-placeholder="Produit"
        v-model="realStockData.item"
      ></vs-input>
      <vs-input
        type="number"
        class="w-full mt-5 mb-4"
        label-placeholder="Quantité"
        @change="TestQte"
        v-on:change="GetTotalPrice"
        v-model="orderedItem.quantity"
      ></vs-input>
      <vs-input
        type="number"
        class="w-full mt-5 mb-4"
        label-placeholder="Prix unitaire"
        v-on:change="GetTotalPrice"
        v-model="orderedItem.unitPrice"
      ></vs-input>
      <vs-input
        type="number"
        class="w-full mt-5 mb-4"
        label-placeholder="Cout total"
        v-model="orderedItem.totalPrice"
      ></vs-input>
      <vs-button :disabled="disabled" class="mr-3" type="filled" @click="AddOrderedItem">Ajouter</vs-button>
      <vs-button color="dark" type="flat">Annuler</vs-button>
    </vs-popup>



  </div>
</template>

<script>
import { AgGridVue } from "ag-grid-vue";
import axios from "axios";
import "@/assets/scss/vuexy/extraComponents/agGridStyleOverride.scss";
import CartDropDownVue from "../../../layouts/components/navbar/components/CartDropDown.vue";
import VuePerfectScrollbar from "vue-perfect-scrollbar";

export default {
  components: {
    AgGridVue,
    CartDropDownVue,
    VuePerfectScrollbar,
  },
  data() {
    return {
      activePromptAddOrderedItemsQuote: 0,
      activePromptAddOrderedItemsInvoice: 0,
      activePromptAddOrderedItemsRental: 0,
      disabled: false,
      cartItems: [],
      cartItemsId: [],
      activePromptAddRealStock: false,
      activePromptEditRealStock: false,
      activePromptAddOrderedItems: false,
      quote: {
        total_ht: ''
      },
      invoice: {
        number: '',
        total_net_ht: '',
        total_ttc: '',
        fiscal: 0.600,
        tva: "",
      },
      rental_voucher: {
        number: '',
        total_net_ht: '',
        total_ttc: '',
        fiscal: 0.600,
        tva: "",
      },
      realStock: {
        item: "",
        quantity: "",
        unitPrice: "",
        totalPrice: "",
        vendor: "",
        vendorTel: "",
      },
      orderedItem: {
        orderedItem: "",
        quantity: "",
        unitPrice: "",
        totalPrice: "",
        status: "not_ordered"
      },
      realStocks: [],
      realStockData: [],
      searchQuery: "",
      gridOptions: {},
      maxPageNumbers: 7,
      gridApi: null,
      defaultColDef: {
        sortable: true,
        editable: true,
        resizable: true,
        suppressMenu: true,
      },
      columnDefs: [
        {
          headerName: "Produit",
          field: "item",
          width: 300,
          filter: true,
          checkboxSelection: true,
          headerCheckboxSelectionFilteredOnly: true,
        },
        {
          headerName: "Quantité",
          field: "quantity",
          filter: true,
          width: 120,
        },
        {
          headerName: "Prix Unitaire",
          field: "unitPrice",
          filter: true,
          width: 175,
        },
        {
          headerName: "Coût",
          field: "totalPrice",
          filter: true,
          width: 175,
        },
        {
          headerName: "Fournisseur",
          field: "vendor",
          filter: true,
          width: 200,
        },
        {
          headerName: "Tél Fournisseur",
          field: "vendorTel",
          filter: true,
          width: 175,
        },
      ],
    };
  },
  computed: {
    paginationPageSize() {
      if (this.gridApi) return this.gridApi.paginationGetPageSize();
      else return 50;
    },
    totalPages() {
      if (this.gridApi) return this.gridApi.paginationGetTotalPages();
      else return 0;
    },
    currentPage: {
      get() {
        if (this.gridApi) return this.gridApi.paginationGetCurrentPage() + 1;
        else return 1;
      },
      set(val) {
        this.gridApi.paginationGoToPage(val - 1);
      },
    },
  },
  methods: {

    //OTHER METHODS
    async DeleteItem(item) {
      await axios
        .delete("/ordereditems/" + item.id + "/", {
          headers: {
            Authorization: `Token ${localStorage.getItem("access_token")}`,
          },
        })
        .then(() => {
          this.GetOrderedItems();
        })
    },
    Add() {
      if (this.activePromptAddOrderedItemsQuote == 1) {
        this.AddQuote()
      }
      else if (this.activePromptAddOrderedItemsInvoice == 1)
      {
        this.AddInvoice()
      }
      else if (this.activePromptAddOrderedItemsRental == 1)
      {
        this.AddRental()
      }
    },
    SetInvoice() {
      this.activePromptAddOrderedItemsInvoice = 1
      this.activePromptAddOrderedItemsQuote = 0
      this.activePromptAddOrderedItemsRental = 0
    },
    SetQuote() {
      this.activePromptAddOrderedItemsQuote = 1
      this.activePromptAddOrderedItemsInvoice = 0
      this.activePromptAddOrderedItemsRental = 0
    },
    SetRental() {
      this.activePromptAddOrderedItemsQuote = 0
      this.activePromptAddOrderedItemsInvoice = 0
      this.activePromptAddOrderedItemsRental = 1
    },
    TestQte() {
      if (this.realStockData.quantity < this.orderedItem.quantity) {
        this.$vs.notify({
          title: "Quantité indisponible",
          text: "veuillez vérifier vos informations",
          iconPack: "feather",
          icon: "icon-alert-circle",
          color: "danger",
        });
        this.disabled= true
      }
      else {
        this.disabled = false
      }
    },
    GetTotalPrice() {
      this.orderedItem.orderedItem = this.realStockData.id;
      this.orderedItem.totalPrice =
        this.orderedItem.quantity * this.orderedItem.unitPrice;
    },

     //ADD METHODS

    async AddQuote() {
      this.cartItems.map((item) => {
        this.quote.total_ht += (item.totalPrice)
      })
      await axios
        .post("/quotes/", {
          total_ht: this.quote.total_ht.toFixed(3),
          quote_status: "En attente",
          items: this.cartItemsId,
        }, {
          headers: {
            Authorization: `Token ${localStorage.getItem("access_token")}`,
          },
        })
        .then(() => {
          this.PutOrderedItems()
          this.GetOrderedItems()
          this.$vs.notify({
            title: "Ajout avec succès",
            color: "success",
          });
          this.$router.push('devis')
        });
    },
    async AddRental() {
      this.cartItems.map((item) => {
        this.rental_voucher.total_net_ht = Number(this.rental_voucher.total_net_ht + item.totalPrice)
      })
      this.rental_voucher.total_ttc = (this.rental_voucher.total_net_ht * 1.09) + Number(this.rental_voucher.fiscal)
      this.rental_voucher.tva = this.rental_voucher.total_ttc - this.rental_voucher.total_net_ht

      await axios
        .post("/rentalVoucher/", {
          total_net_ht: this.rental_voucher.total_net_ht.toFixed(3),
          total_ttc: this.rental_voucher.total_ttc.toFixed(3),
          payment_status: 'Impayée',
          tva: this.rental_voucher.tva.toFixed(3),
          fiscal: this.rental_voucher.fiscal,
          rental_status: "En attente",
          items: this.cartItemsId,
        }, {
          headers: {
            Authorization: `Token ${localStorage.getItem("access_token")}`,
          },
        })
        .then(() => {
          this.PutOrderedItems()
          this.GetOrderedItems()
          this.$vs.notify({
            title: "Ajout avec succès",
            color: "success",
          });
          this.$router.push('bon-location')
        });
    },
    async AddInvoice() {
      this.cartItems.map((item) => {
        this.invoice.total_net_ht = Number(this.invoice.total_net_ht+item.totalPrice)
      })
      this.invoice.total_ttc = (this.invoice.total_net_ht * 1.09) + Number(this.invoice.fiscal)
      this.invoice.tva = this.invoice.total_ttc - this.invoice.total_net_ht

      await axios
        .post("/invoices/", {
          total_net_ht: this.invoice.total_net_ht.toFixed(3),
          total_ttc: this.invoice.total_ttc.toFixed(3),
          payment_status: 'Impayée',
          tva: this.invoice.tva.toFixed(3),
          fiscal: this.invoice.fiscal,
          invoice_status: "En attente",
          items: this.cartItemsId,
        }, {
          headers: {
            Authorization: `Token ${localStorage.getItem("access_token")}`,
          },
        })
        .then(() => {
          this.PutOrderedItems()
          this.GetOrderedItems()
          this.$vs.notify({
            title: "Ajout avec succès",
            color: "success",
          });
          this.$router.push('facture')
        });
    },
    async AddOrderedItem() {
      await axios
        .post("/ordereditems/", this.orderedItem, {
          headers: {
            Authorization: `Token ${localStorage.getItem("access_token")}`,
          },
        })
        .then(() => {
          this.$vs.notify({
            title: "Ajout avec succès",
            color: "success",
          });
          this.orderedItem.quantity = "";
          this.orderedItem.unitPrice = "";
          this.orderedItem.totalPrice = "";
          this.GetOrderedItems();
          this.activePromptEditRealStock = false;
          this.activePromptAddOrderedItems = false;
        })
        .catch(() => {
          this.$vs.notify({
            title: "Erreur d'ajout",
            text: "veuillez vérifier vos informations",
            iconPack: "feather",
            icon: "icon-alert-circle",
            color: "danger",
          });
          this.activePromptAddOrderedItems = true;
        });
    },
    async AddRealStock() {
      this.realStock.totalPrice = this.realStock.quantity * this.realStock.unitPrice;
      await axios
        .post("/realstocks/", this.realStock, {
          headers: {
            Authorization: `Token ${localStorage.getItem("access_token")}`,
          },
        })
        .then(() => {
          this.$vs.notify({
            title: "Ajout avec succès",
            color: "success",
          });
          this.realStock.item = "";
          this.realStock.quantity = "";
          this.realStock.unitPrice = "";
          this.realStock.totalPrice = "";
          this.realStock.vendor = "";
          this.realStock.vendorTel = "";
          this.GetRealStocks();
          this.activePromptAddRealStock = false;
        })
        .catch((error) => {
          this.$vs.notify({
            title: "Erreur d'ajout",
            text: "veuillez vérifier vos informations",
            iconPack: "feather",
            icon: "icon-alert-circle",
            color: "danger",
          });
          this.activePromptAddRealStock = true;
        });
    },

    //PUT METHODS


    async PutOrderedItems() {
      this.cartItems.map((item) => {
        axios
          .put("/ordereditems/" + item.id + "/",
          {
            orderedItem: item.orderedItem,
            quantity: item.quantity,
            totalPrice: item.totalPrice,
            unitPrice: item.unitPrice,
            status: "ordered"
          }, {
            headers: {
              Authorization: `Token ${localStorage.getItem("access_token")}`,
            },
          })
      })
    },
    async PutRealStock() {
      this.realStockData.totalPrice =
        this.realStockData.quantity * this.realStockData.unitPrice;
      await axios
        .put("/realstocks/" + this.realStockData.id + "/", this.realStockData, {
          headers: {
            Authorization: `Token ${localStorage.getItem("access_token")}`,
          },
        })
        .then(() => {
          this.$vs.notify({
            title: "Modification avec succès",
            color: "success",
          });
          this.GetRealStocks();
          this.activePromptEditRealStock = false;
        })
        .catch(() => {
          this.$vs.notify({
            title: "Erreur de modification",
            text: "veuillez vérifier vos informations",
            iconPack: "feather",
            icon: "icon-alert-circle",
            color: "danger",
          });
          this.activePromptEditRealStock = true;
        });
    },

    //DELETE METHODS

    DeleteRealStock() {
      this.$vs.dialog({
        type: "confirm",
        color: "danger",
        title: `Confirmation de la suppression`,
        text: `Supprimer "${this.realStockData.item}"`,
        accept: this.ConfirmDeleteRealStock,
        acceptText: "Supprimer",
        cancelText: "Annuler",
      });
    },
    async ConfirmDeleteRealStock() {
      await axios
        .delete("/realstocks/" + this.realStockData.id + "/", {
          headers: {
            Authorization: `Token ${localStorage.getItem("access_token")}`,
          },
        })
        .then(() => {
          this.$vs.notify({
            title: "Suppression avec succès",
            color: "success",
          });
          this.GetRealStocks();
          this.activePromptEditRealStock = false;
        })
        .catch(() => {
          this.$vs.notify({
            title: "Erreur de Suppression",
            text: "veuillez essayer plus tard",
            iconPack: "feather",
            icon: "icon-alert-circle",
            color: "danger",
          });
          this.activePromptEditRealStock = true;
        });
    },

    //GET METHODS

    async GetRealStocks() {
      await axios
        .get("/realstocks/", {
          headers: {
            Authorization: `Token ${localStorage.getItem("access_token")}`,
          },
        })
        .then((res) => {
          this.realStocks = res.data;
        })
    },
    async GetOrderedItems() {
      await axios
        .get("/ordereditems/?status=not_ordered", {
          headers: {
            Authorization: `Token ${localStorage.getItem("access_token")}`,
          },
        })
        .then((res) => {
          this.cartItems = res.data;
          this.cartItems.map(item => {
            this.cartItemsId.push(item.id)
          })
        });
    },
    onSelectionChanged() {
      const dataObj = this.gridApi.getSelectedRows();
      this.realStockData = dataObj[0];
      this.activePromptEditRealStock = true;
    },
    updateSearchQuery(val) {
      this.gridApi.setQuickFilter(val);
    },
  },
  mounted() {
    this.GetOrderedItems();
    this.GetRealStocks();
    this.gridApi = this.gridOptions.api;
  },
};
</script>
